.social__wrapper {
  // width: 320px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (min-width: @tablet-width) {
    width: 708px;
    padding: 58px 0 55px 0;
    flex-direction: row;
    justify-content: space-between;
  }

  @media (min-width: @desktop-width) {
    width: 1220px;
  }
}

.social {
  margin: 0;
  padding: 20px 94px;
  display: flex;
  align-items: center;
  list-style-type: none;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    padding: 0;
  }
}

.social__logo {
  padding: 39px 96px 32px 96px;

  @media (min-width: @tablet-width) {
    padding: 0;
  }
}

.social__link {
  text-decoration: none;
}
